import React from "react";
import "./Footer.css";

const Footer = () => {
    return(
        <div className = "footer">
            <div className = "footer p">
                <p> Copyright @
                    <script> 
                       function(){
                            document.write(new Date().getFullYear())
                        }
                    </script>
                </p>
           </div>
        </div>
    )
}

export default Footer;