import React from "react";
import Header from "./Header";
import Footer from "./Footer";
import Entry from "./Note";
import notes from "../notes";
import "./App.css";

function App() {
  return (
    <div className = "App">
      <Header />
      <Footer />
      {notes.map(function (message) {
        return (
          <Entry
            key = {notes.key}
            title = {notes.title}
            content = {notes.content}
          />
        );
      })}
    </div>
  );
}

export default App;